#header{position:fixed;left:0;top:0;z-index:99998;width:100%;height:80px;text-align:center;background-color:#fff;box-shadow:0 0 2px rgba(0,0,0,0.3);transition:height .3s}
#header.over{height:340px}
#header::after{content:"";display:block;position:absolute;left:0;top:80px;z-index:99999;width:100%;height:1px;background-color:#eaeaea}
#header .inner{height:80px;position:relative;margin:0 auto;width:1400px;background-color:#fff}
#header .menu{overflow:hidden;height:80px;background-color:#fff}
#header .menu.over{height:340px;width:100%;transition:height .3s}
#header .logo{background:url(data:image/png;base64,) no-repeat 0 0;position:absolute;left:0;top:20px;line-height:40px}
#header .utils{position:absolute;right:0;top:0;z-index:99999}
#header .utils .lang{float:left}
#header .utils .lang > ul > li > a{display:block;line-height:80px}
#header .utils .lang ul li{overflow:hidden}
#header .utils .lang ul ul{height:0;transition:height .3s}
#header .utils .lang ul ul a{display:block}
#header .utils .lang.active ul ul{height:100px}
#header .utils .lang ul ul li{line-height:50px}
#header .utils .lang ul ul li:first-child{background-color:#666}
#header .utils .lang ul ul li:last-child{background-color:#fff}
#gnb{margin-left:70px}
#gnb > ul > li{display:inline-block;position:relative}
#gnb > ul > li > a{display:block;position:relative;padding:0 50px;line-height:80px;font-size:17px;font-weight:500;color:#333}
#gnb > ul > li > a::after{content:"";display:block;position:absolute;left:0;bottom:0;width:100%;height:3px;background-color:#ac4eff;-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}
#gnb > ul > li.over > a::after{-webkit-transform:scaleX(1);transform:scaleX(1)}
#gnb li ul{position:absolute;left:0;top:80px;padding:20px 0;width:100%;height:260px;text-align:center;border-left:1px solid #eaeaea}
#gnb li:last-child ul{border-right:1px solid #eaeaea}
#gnb li ul a{display:inline-block;position:relative;line-height:38px;font-size:15px;color:#666}
#gnb li ul li a:hover,#gnb li ul li a:focus{color:#ac4eff}
#gnb li ul li a::after{content:"";position:absolute;left:0;bottom:6px;width:100%;height:1px;background-color:#ac4eff;-webkit-transform:scaleX(0);transform:scaleX(0);transition:-webkit-transform .3s;transition:transform .3s;transition:transform .3s, -webkit-transform .3s}
