.category3 .top{min-width:1400px;margin:0 auto;margin-bottom:60px;height:78px;box-sizing:border-box;position:relative}
.category3 .top::after{content:"";display:block;width:100%;height:1px;background-color:#eaeaea;position:absolute;left:0;bottom:0}
.category3 .top_inner{width:1400px;height:78px;margin:0 auto}
.category3 .top .logo{float:left}
.category3 .top .logo a{display:block}
.category3 .top .sns{float:right}
.category3 .top .sns .inner{height:40px;overflow:hidden}
.category3 .top .sns li{display:inline-block;margin-left:30px;display:inline-block}
.category3 .top .sns img{transition:.5s}
.category3 .top .sns a:hover img{-webkit-transform:translateY(-40px);transform:translateY(-40px)}
.category3 .top .sns a:focus img{-webkit-transform:translateY(-40px);transform:translateY(-40px)}
.category3 .top .sns li a{display:block}
.category3 .top .sns li a span{line-height:40px;margin-left:10px;font-size:14px}
.category3 .title{text-align:center;margin-bottom:40px;position:relative}
.category3 .title::after{content:"";display:block;width:100px;height:100px;background:url(data:image/png;base64,) no-repeat 0 0;-webkit-transform:rotateZ(90deg);transform:rotateZ(90deg);position:absolute;left:46%;top:-30px}
.category3 .title p{font-size:40px;font-weight:600;margin-bottom:10px}
.category3 .banner{position:relative}
.category3 .banner .swiper-container{width:100%;height:100%;position:relative}
.category3 .banner .swiper-slide{text-align:center;font-size:18px;background:#fff;overflow:hidden;display:flex;justify-content:center;align-items:center}
.category3 .banner .swiper-slide img{display:block;width:100%;height:100%;object-fit:cover;-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform .5s;transition:transform .5s;transition:transform .5s, -webkit-transform .5s}
.category3 .banner .swiper-slide img:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}
.category3 .controller{position:absolute;right:0;top:-50px}
.category3 .controller li{display:block;float:left;border:1px solid #000}
.category3 .controller li a{display:block}
.category4{min-width:1400px;padding:120px 0;margin-top:120px;background-color:#f8f8f8}
.category4 .inner{width:1400px;height:670px;margin:0 auto}
.category4 .logo a{display:block;width:260px}
.category4 .content{width:1400px;height:500px;position:relative}
.category4 .content .left_photo img{width:525px;height:400px;position:absolute;left:0;top:50px;z-index:3}
.category4 .content .photo_frame img{width:900px;height:500px;position:absolute;right:0;top:0;z-index:2}
.category4 .content .right_photo img{width:460px;height:320px;position:absolute;right:225px;top:138px;z-index:1}
.category4 .bottom{width:1400px;height:129px;position:relative}
.link{width:300px;height:70px;line-height:70px;background-color:#ac4eff;position:absolute;bottom:0;left:550px}
.link a{display:block;text-align:center;color:#fff}
footer{min-width:1400px;background-color:#f3f3f3}
footer .inner{margin:0 auto;width:1400px;height:124px}
footer .copyright{float:left;margin-top:15px}
footer .copyright p{color:#9f9f9f;font-size:14px}
footer .f_logo{float:right;margin-top:61px}
